/* =============================
   PROFESSIONAL ADMIN NAVBAR
   ============================= */

.admin-navbar {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
  padding: 0.8rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
  position: sticky;
  top: 0;
  z-index: 1000;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  height: 70px;
}

.admin-navbar-left {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.admin-navbar-brand {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.logo {
  border-radius: 50%;
  height: 2.8rem;
  width: 2.8rem;
  border: 2px solid rgba(255, 255, 255, 0.85);
  object-fit: cover;
}

.brand-name {
  font-weight: 700;
  font-size: 1.25rem;
  letter-spacing: 0.5px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.store-switch-wrapper {
  position: relative;
  display: inline-block;
}

/* Nav Links */
.admin-navbar-nav {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 1.2rem;
  margin: 0;
  padding: 0;
}

.admin-navbar-nav li {
  display: inline-block;
}

.admin-navbar-nav li:last-child {
  margin-left: auto;
}

.admin-navbar-nav a {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 0.3px;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  transition: all 0.25s ease-in-out;
  background: transparent;
}

/* Professional Hover (subtle gradient shift) */
.admin-navbar-nav a:hover {
  background: rgba(181, 178, 178, 0.15);
  color: #fff;
  transform: translateY(-1px);
}

/* Active State  solid highlight with gradient */
.admin-navbar-nav .active {
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.281),
    rgba(255, 255, 255, 0.186)
  );
  border-radius: 8px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.15);
  color: #fff;
}

/* Remove glowing & ensure consistency */
.admin-navbar-nav a:focus,
.admin-navbar-nav .active:focus {
  outline: none;
  box-shadow: none;
}

/* Store Dropdown Styles */
.clickable-store {
  cursor: pointer;
  transition: all 0.2s ease;
}

.clickable-store:hover {
  background: rgba(255, 255, 255, 0.25);
  transform: scale(1.03);
}

.store-name {
  background: rgba(255, 255, 255, 0.2);
  padding: 0.4rem 0.8rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.3rem;
  backdrop-filter: blur(10px);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.store-name:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-1px);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.15);
  border-color: rgba(255, 255, 255, 0.5);
}

.store-dropdown {
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  min-width: 160px;
  z-index: 1000;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.08);
  animation: dropdownFadeIn 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(20px);
}

@keyframes dropdownFadeIn {
  from {
    opacity: 0;
    transform: translateY(-15px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.store-option {
  padding: 10px 14px;
  color: #2d3748;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.85rem;
}

.store-option:hover {
  background: linear-gradient(135deg, #f7fafc, #edf2f7);
  color: #1a202c;
  transform: translateX(2px);
}

.store-option::before {
  content: '';
  font-size: 0.9rem;
}

/* =============================
   ANIMATED LOGOUT BUTTON
   ============================= */

.logout-wrapper {
  display: flex;
  align-items: center;
}

.logout-wrapper-left {
  display: flex;
  align-items: center;
  margin-left: 1rem;
}

/* Compact Logout Button for Right Side */
.logout-wrapper-compact {
  display: flex;
  align-items: center;
  margin-left: 0.5rem;
}

.logout-compact-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  padding: 0.4rem 0.8rem;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  background-color: rgba(255, 65, 65, 0.9);
  color: white;
  font-size: 0.8rem;
  font-weight: 600;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.logout-compact-btn:hover {
  background-color: rgb(255, 65, 65);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(255, 65, 65, 0.3);
}

.logout-sign-compact {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logout-sign-compact svg {
  width: 12px;
  height: 12px;
}

.logout-sign-compact svg path {
  fill: white;
}

.logout-text-compact {
  font-size: 0.8rem;
  font-weight: 600;
  white-space: nowrap;
}

/* Disabled state for compact button */
.logout-compact-btn:disabled {
  background: rgba(108, 117, 125, 0.8);
  cursor: not-allowed;
  transform: none;
}

.logout-compact-btn:disabled:hover {
  transform: none;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
}

.logout-animated-btn {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 45px;
  height: 45px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  background-color: rgb(255, 65, 65);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
}

.logout-sign {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.logout-sign svg {
  width: 17px;
}

.logout-sign svg path {
  fill: white;
}

.logout-text {
  position: absolute;
  right: 0;
  width: 0;
  opacity: 0;
  color: white;
  font-size: 0.95rem;
  font-weight: 600;
  white-space: nowrap;
  transition: all 0.3s ease;
}

/* Hover expand */
.logout-animated-btn:hover {
  width: 120px;
  border-radius: 40px;
}

.logout-animated-btn:hover .logout-sign {
  width: 30%;
  padding-left: 14px;
}

.logout-animated-btn:hover .logout-text {
  opacity: 1;
  width: 70%;
  padding-right: 14px;
}

/* Disabled state */
.logout-animated-btn:disabled {
  background: #6c757d;
  cursor: not-allowed;
}

.logout-animated-btn:disabled:hover {
  width: 45px;
}


/* Responsive Design */
@media (max-width: 768px) {
  .admin-navbar {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
    padding: 1rem;
  }

  .admin-navbar-left {
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
  }

  .logout-wrapper-left {
    margin-left: 0;
    margin-top: 0.5rem;
  }

  .admin-navbar-nav {
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    margin-top: 0.8rem;
  }

  .admin-navbar-nav a {
    padding: 0.6rem 1rem;
  }

  .logout-compact-btn {
    padding: 0.3rem 0.6rem;
    font-size: 0.75rem;
  }

  .logout-sign-compact svg {
    width: 10px;
    height: 10px;
  }

  .store-name {
    padding: 0.3rem 0.7rem;
    font-size: 0.8rem;
  }
  
  .store-dropdown {
    right: 0;
    left: auto;
    min-width: 140px;
  }
}